export default {
	data() {
		return {
			dataList:[]
		}
	},
  //数据初始化
  mounted(){
		this.getList()
	},
  //定义一些函数名
  methods:{
    //
		getList(){
			const data =[
				{title:'卫浴'},
				{title:'开关'},
				{title:'地板'},
				{title:'墙纸'},
				{title:'瓷砖'}
			]
			this.dataList=data
		},
    delete() {
      this.getList()
    }
	}
}
