export default {
    name: 'HrBreadcrumd',
    props: {
        separator: {
            type: String,
            default: '>'
        }
    },
    watch: {
        separator() {
            this.updateChildren()
        }
    },
    mounted() {
        this.updateChildren()
    },
    methods: {
        updateChildren() {
            this.$children.forEach((child) => {
                child.separator = this.separator
            })
        }
    }
}


